.fs-dialog-modal {
  --fs-dialog-shadow: rgba(0, 0, 0, 0.5);
  --fs-dialog-border-color: grey;
  --fs-dialog-border-size: 0px;
}

.fs-dialog-modal {
  box-shadow: 0px 10px 20px 5px var(--fs-dialog-shadow);
  transform: translateY(-5px);
  border: var(--fs-dialog-border-size) solid var(--fs-dialog-border-color);
  border-radius: 6px;
  padding: 0px;
  margin: auto;
}

.fs-dialog-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 4px;
  padding: 4px;
  border-radius: 6px, 6px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.fs-dialog-body {
  margin-bottom: 2px;
  padding: 6px;
  border-radius: 3px;
  max-height: calc(100vh - 280px);
  overflow-y: auto;
}

.fs-dialog-hr {
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 4px;
  margin-right: 4px;
}

.fs-dialog-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px;
}

.fs-dialog-margin-auto {
  flex: 1 1 auto;
}

.fs-dialog-ml-2 {
  margin-left: 2px;
}

.fs-dialog-input {
  width: 100%;
}

.fs-dialog-input-label {
  margin: 0;
  padding: 0;
}

.fs-dialog-input:focus {
  outline: none;
}

.fs-dialog-auto {
  min-width: 300px;
  max-width: calc(100vw - 100px);
}

.fs-dialog-buttons-center {
  justify-content: center;
}

.fs-dialog-pointer {
  cursor: pointer;
}